.main-menu-title {
  display: none !important;
  padding: 0 10px;
  color: #fff;
  text-transform: uppercase;
  font-weight: 700;
  cursor: pointer;
}
@media (max-width: 991px) {
  .main-menu-title {
    display: flex !important;
    align-items: center;
  }
}
.main-menu-title__icon {
  align-items: center;
  margin-right: 1em;
  width: 1em;
  line-height: 1em;
  display: block;
  vertical-align: middle;
  height: 3px;
  background: #fff;
  position: relative;
  transition: background .3s .5s;
}
.main-menu-title__icon:before,
.main-menu-title__icon:after {
  width: 100%;
  height: 3px;
  background: #fff;
  position: absolute;
  left: 0;
  content: '';
  transform-origin: 50% 50%;
  transition: top .3s .6s, transform .3s;
}
.main-menu-title__icon:before {
  top: 5px;
}
.main-menu-title__icon:after {
  top: -5px;
}
.main-menu-title__icon--close {
  transition: background .3s 0;
  background: transparent;
}
.main-menu-title__icon--close:before,
.main-menu-title__icon--close:after {
  transition: top .3s, transform .3s .5s;
  top: 0;
}
.main-menu-title__icon--close:before {
  transform: rotate3d(0, 0, 1, 45deg);
}
.main-menu-title__icon--close:after {
  transform: rotate3d(0, 0, 1, -45deg);
}
